export const lampArray:string[] = [
  "FAILED","ASSIST","EASY","CLEAR","HARD","EX-HARD","FULLCOMBO","UNKNOWN"
];
export const lampCSVArray:string[] = [
  "FAILED","ASSIST CLEAR","EASY CLEAR","CLEAR","HARD CLEAR","EX HARD CLEAR","FULLCOMBO CLEAR"
];

export const convertClearState = (original:string|number,direction:number):string|number=>{
  if(direction === 0){
    // csv to num
    if(typeof original !== "string"){return 0;}
    return lampCSVArray.indexOf(original);
  }else{
    if(typeof original !== "number"){return 1;}
    return lampCSVArray[original];
  }
}

export const _prefix = (diff:string):string=> diff === "hyper" ? "(H)" : diff === "leggendaria" ? "()" : "";

export const _prefixFromNum = (difficulty:string):string=>{
  let prefix:string = "";
  if(difficulty === "3" || difficulty === "8"){
    prefix = "(H)";
  }
  if(difficulty === "10" || difficulty === "11"){
    prefix = "()";
  }
  return prefix;
}

export const difficultyDiscriminator = (difficulty:string):string=>{
  let diff:string = "another";
  if(difficulty === "3" || difficulty === "8"){
    diff = "hyper";
  }
  if(difficulty === "10" || difficulty === "11"){
    diff = "leggendaria";
  }
  return diff;
}

export const difficultyParser = (difficulty: string,isSingle: number):string=>{
  if(difficulty === "another"){
    if(isSingle === 1){
      return "4";
    }else{
      return "9";
    }
  }else if(difficulty === "hyper"){
    if(isSingle === 1){
      return "3";
    }else{
      return "8";
    }
  }else if(difficulty === "leggendaria"){
    if(isSingle === 1){
      return "10";
    }else{
      return "11";
    }
  }
  return "4";
}

export const getSongSuffixForIIDXInfo = (name:string,difficulty:string):string=>{
  if(difficulty === "3" || difficulty === "8"){
    return "[H]";
  }
  if(difficulty === "4" || difficulty === "9"){
    return "[A]";
  }
  if(difficulty === "10" || difficulty === "11"){
    switch(name){
      default:
        return "[A]";
      case "Ancient Scapes":
      case "Close the World feat.aru":
      case "Feel The Beat":
      case "invoker":
      case "Sigmund":
      case "Verflucht":
      case "":
        return "LEGGENDARIA[A]";
    }
  }
  return "";
}


export const convertLeggendariaStates = (name:string,difficulty:string):{name:string,difficulty:string}=>{
  const leggendariaSongs:string[] = [
    "ABSOLUTE",
    "Clione",
    "RED ZONE",
    "spiral galaxy",
    "Little Little Princess",
    "CONTRACT",
    "waxing and wanding",
    "KAMAITACHI",
    "VANESSA",
    "Blue Rain",
    "ICARUS",
    "THE DEEP STRIKER",
    "bertreffen",
    "Kung-fu Empire",
    "naughty girl@Queen's Palace",
    "THANK YOU FOR PLAYING",
    "",
    "SOLID STATE SQUAD",
    "Golden Palms",
    "QUANTUM TELEPORTATION",
    "Howling",
    "LUV CAN SAVE U",
    "",
    "",
    "",
    "Ancient ScapesLEGGENDARIA",
    "Close the World feat.aruLEGGENDARIA",
    "Feel The BeatLEGGENDARIA",
    "invokerLEGGENDARIA",
    "SigmundLEGGENDARIA",
    "VerfluchtLEGGENDARIA",
    "LEGGENDARIA",
    "",
    "Beat Radiance",
    "CHRONO DIVER -NORNIR-",
    "Cosmic Cat",
    "EBORY & IVORY",
    "",
    "",
    "Damage Per Second",
    "STARLIGHT DANCEHALL",
    "Amazing Mirage",
    " ft. Kanae Asaba",
  ]
  let newName = name,newDifficulty = difficulty;
  if(difficulty !== "another"){
    return {
      name:newName,
      difficulty:newDifficulty
    }
  }
  if(leggendariaSongs.indexOf(name) > -1){
    newName = name.replace("LEGGENDARIA","").replace("","");
    newDifficulty = "leggendaria";
  }
  return {
    name:newName,
    difficulty:newDifficulty
  }
}
